@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

body{
    background: #e4e4e4;
}

.container{
    width: 90%;
    max-width: 800px;
    margin: auto;
    padding: 0 20px;
}

h1{
    margin-top: 40px;
    color: #264653;
}

.sub{
    border-bottom: 2px solid #264653;
    margin-top: 20px;
    width: 10%;
}

input{
    margin-top: 40px;
    width: 100%;
}

.GifGridContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.gif {
    width: 33%;
    height: 200px;
    margin-bottom: 15px;
}

.gif img{
    object-fit: cover;
    width: 100%;
    height: 80%;
    vertical-align: middle;
    
}

.gif p{
    border-top: 1px solid rgba(0, 0, 0, .3);
    padding: 5px;
    text-align: center;
    font-weight: 300;
    text-transform: capitalize;
}

h3{
    padding: 15px 0;
    text-transform: capitalize;
}